import { createRef } from "react";
import { Component } from "react";
import contactService from "../services/contactService";

export class ContactEdit extends Component {
    inputRef = createRef()
    state = {
        contact: null,
    }

    async componentDidMount() {
        try {
            const id = this.props.match.params.id
            const contact = id ? await contactService.getContactById(id) : contactService.getEmptyContact()
            this.setState({ contact }, () => this.inputRef.current.focus())
        } catch (err) {
            console.log('some err')
        }
    }
    handleChange = ({ target }) => {
        const field = target.id
        // const value = target.type === number ? +target.value : target.value
        const value = target.value
        this.setState(prevState => ({ contact: { ...prevState.contact, [field]: value } }))
    }
    onSaveContact = async (ev) => {
        ev.preventDefault()
        const { contact } = this.state
        await contactService.saveContact(contact)
        this.props.history.push('/')
    }
    render() {
        const {contact}=this.state
        if(!contact)return <div>loading</div>
        return (
            <section className="contact-edit">
                <h2>edit</h2>
                <form className="contact-edit-form">
                    <label htmlFor="name">Name</label>
                    <input
                        type="text"
                        value={contact.name}
                        id="name"
                        onChange={this.handleChange}
                        ref={this.inputRef}
                    />
                    <label htmlFor="email">Email</label>
                    <input
                        type="email"
                        value={contact.email}
                        id="email"
                        onChange={this.handleChange}
                    />
                    <label htmlFor="phone">Phone</label>
                    <input
                        type="text"
                        value={contact.phone}
                        id="phone"
                        onChange={this.handleChange}
                    />
                    <button onClick={this.onSaveContact}>Save</button>
                </form>
            </section>
        )
    }

}