import { connect } from 'react-redux'
import { Component } from "react";
import contactService from "../services/contactService";
import { ContactList } from "../cmps/ContactList";
import { ContactFilter } from "../cmps/ContactFilter";
import { Link } from 'react-router-dom';
import { loadContacts,removeContact, setFilterBy } from '../store/actions/contactActions'
class _Contacts extends Component {
    // state = {
    //     contacts: null,
    //     filterBy: {
    //         term: '',
    //     }
    // }
    componentDidMount() {
        this.props.loadContacts()
    }
    // async loadContacts() {
    //     const contacts = await contactService.getContacts(this.state.filterBy)
    //     this.setState({ contacts })
    // }
  removeContact = async (contactId) => {
      this.props.removeContact(contactId)
        // await contactService.deleteContact(contactId)
        // this.loadContacts()
    }
    onChangeFilter = (filterBy) => {
        console.log(filterBy)
        // this.setState({ filterBy }, this.loadContacts)
        this.props.setFilterBy(filterBy)
        this.props.loadContacts()

    }
    render() {
        const { contacts } = this.props
        if (!contacts) return <div>loading...</div>
        return (
            <section className="contacts">
                <h2>coin app</h2>
                <Link to="/contact/edit">add contact</Link>
                <ContactFilter onChangeFilter={this.onChangeFilter} />
                <ContactList contacts={contacts} removeContact={this.removeContact}></ContactList>
            </section>
        )
    }
}


const mapStateToProps = state => {
    return {
        contacts: state.contactModule.contacts,
        filterBy: state.contactModule.filterBy
    }
}

const mapDispatchToProps = {
    loadContacts,
    removeContact,
    setFilterBy
}

// Connects the store with the component, injects it to the props
export const Contacts = connect(mapStateToProps, mapDispatchToProps)(_Contacts)