import { Component } from "react";
import { connect } from 'react-redux'
import contactImg from '../assets/img/contact.png'
import { Link } from "react-router-dom";
import{getContactById,removeContact}from '../store/actions/contactActions'
class _ContactDetails extends Component {
    state = {
        contact: null,
    }
    componentDidMount() {
        this.loadContact()
    }
    loadContact = async () => {
        const id = this.props.match.params.id
        this.props.getContactById(id)
        // const contact = await contactService.getContactById(id)
        // this.setState({ contact })
    }
    goBack=()=>{
        this.props.history.push('/contact')
        // this.props.history.goBack()
    }
    render() {
        const { contact } = this.props
        if (!contact) return <div>bla bla</div>
        return (
            <section className="contact-details">
                <img src={contactImg} alt="" />
                <h2>{contact.name}</h2>
                <h3>{contact.email}</h3>
                <h3>{contact.phone}</h3>
                <p>Lorem ipsum dolor
                    sit amet consectetur adipisicing elit. Dicta </p>
                <button onClick={this.goBack}>Back</button>
                <Link to={'/contact/edit/' + contact._id}>Edit</Link>
            </section>
        )
    }
}


const mapStateToProps = state => {
    return {
      contact: state.contactModule.currContact,
      loggedInUser: state.userModule.loggedInUser
    }
  }
  const mapDispatchToProps = {
    getContactById,
    removeContact,
  }
  export const ContactDetails = connect(mapStateToProps, mapDispatchToProps)(_ContactDetails)