import { ContactPreview } from "./ContactPreview"

export function ContactList({contacts,removeContact}) {
    return (
        // <div className="contact-list">
        //     {contacts.map(contact=>(<ContactPreview contact={contact} key={contact._id}/>))}
        // </div>
        <ul className="contact-list">
               {contacts.map((contact)=>(<ContactPreview removeContact={removeContact} contact={contact} key={contact._id}/>))}
        </ul>
    )
}